.device-desktop .card-backdrop {
  -webkit-backdrop-filter: none;
          backdrop-filter: none;
  background-color: rgba(255, 255, 255, 0.55);
}
.device-desktop.theme-dark .card-backdrop {
  background-color: rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) and (min-height: 670px) {
.appstore-card-grid {
    --card-grid-gap: 30px;
    --cards-per-row: 2;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: var(--card-grid-gap);
    -webkit-column-gap: var(--card-grid-gap);
       -moz-column-gap: var(--card-grid-gap);
            column-gap: var(--card-grid-gap);
    grid-row-gap: var(--card-grid-gap);
    row-gap: var(--card-grid-gap);
    margin-left: calc(var(--f7-card-expandable-margin-horizontal) + var(--f7-safe-area-left));
    margin-right: calc(var(--f7-card-expandable-margin-horizontal) + var(--f7-safe-area-right));
    margin-top: var(--f7-card-expandable-margin-vertical);
    margin-bottom: var(--card-grid-gap);
}
.appstore-card-grid .appstore-card {
    margin: 0;
    width: 100%;
}
}
@media (min-width: 1024px) {
.appstore-card-grid {
    --card-grid-gap: 50px;
}
}
@media (min-width: 1500px) {
.appstore-card-grid {
    --card-grid-gap: 50px;
    --cards-per-row: 3;
    grid-template-columns: 1fr 1fr 1fr;
}
}
.appstore-card {
  height: 410px!important;
  max-height: 100vh;
  --f7-card-content-padding-horizontal: 20px;
  --f7-card-content-padding-vertical: 30px;
  --f7-card-expandable-font-size: 20px;
  --f7-card-expandable-font-weight: 500;
  --f7-card-expandable-max-width: none;
  --f7-card-expandable-tablet-height: 92vh;
  --f7-card-expandable-tablet-border-radius: 13px;
  --card-offset: calc(40px + var(--f7-safe-area-left) + var(--f7-safe-area-right));
  --card-width: calc(100% - var(--f7-safe-area-left) - var(--f7-safe-area-right) - 40px);
}
@media (max-width: 767px), (max-height: 669px) {
.appstore-card {
    margin-top: 0!important;
}
}
@media (min-width: 768px) and (min-height: 670px) {
.appstore-card {
    --card-width: calc((100vw - var(--f7-safe-area-left) - var(--f7-safe-area-right) - var(--f7-card-expandable-margin-horizontal) * 2 - var(--card-grid-gap) * (var(--cards-per-row) - 1)) / var(--cards-per-row));
    --card-offset: calc(670px - var(--card-width));
}
.appstore-card .card-content {
    --f7-safe-area-left: 0px;
    --f7-safe-area-right: 0px;
    --f7-card-content-padding-horizontal: 35px;
}
}
.theme-dark .appstore-card {
  --f7-card-expandable-box-shadow: none;
}
.appstore-card.card-opening,
.appstore-card.card-closing,
.appstore-card.card-transitioning {
  transition-duration: 300ms;
}
.appstore-card.card-opening .card-content {
  transition-duration: 350ms;
}
.appstore-card.card-closing .card-content {
  transition-duration: 400ms;
}
.appstore-card-close-button {
  position: absolute;
  top: calc(16px + var(--f7-safe-area-top));
  right: calc(var(--f7-card-content-padding-horizontal) + var(--f7-safe-area-right));
}
.appstore-card-header {
  height: 410px;
  max-height: 100vh;
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}
.appstore-card-header > img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  z-index: -1;
  background-color: #ccc;
  transform: translateX(calc(-1 * var(--card-offset) / 2));
}
.appstore-card-header-text {
  padding: 16px var(--f7-card-content-padding-horizontal);
  width: 100%;
  box-sizing: border-box;
  transform: translate3d(0, 0, 0);
}
.appstore-card-header-text-top .appstore-card-header-text {
  margin-bottom: auto;
}
.appstore-card-header .apps-table-list {
  padding: 16px var(--f7-card-content-padding-horizontal);
  display: block;
  -webkit-backdrop-filter: blur(30px) saturate(210%);
          backdrop-filter: blur(30px) saturate(210%);
  margin-top: 0;
  width: 100%;
  box-sizing: border-box;
  color: #fff;
  position: relative;
  z-index: 10;
  --f7-list-item-text-text-color: rgba(255, 255, 255, 0.55);
  --f7-list-item-padding-vertical: 0px;
}
.appstore-card-header .apps-table-list-button {
  transform: translateX(calc(-1 * var(--card-offset)));
}
.appstore-card-header .apps-table-list .button {
  --f7-button-bg-color: #fff;
}
.appstore-card-header .apps-table-list ul {
  padding: 0;
  display: block;
  height: unset;
  flex-shrink: 0;
  max-width: none;
}
.appstore-card-header .apps-table-list ul:after {
  display: none !important;
}
.appstore-card-header .apps-table-list li {
  max-width: none;
  width: auto;
  padding: 0;
  margin: 0;
}
.appstore-card-header .apps-table-list-title {
  margin-right: 20px;
  -webkit-line-clamp: 1;
  color: inherit;
}
.appstore-card-header .apps-table-list-button span,
.appstore-card-header .apps-table-list .item-text {
  color: inherit;
  opacity: 0.55;
}
.appstore-card-header .apps-table-list-image {
  width: 48px;
  height: 48px;
}
.appstore-card.card-opened .appstore-card-header .apps-table-list-button,
.appstore-card.card-opening .appstore-card-header .apps-table-list-button,
.appstore-card.card-opened .appstore-card-header > img,
.appstore-card.card-opening .appstore-card-header > img {
  transform: translateX(0);
}
.appstore-card.card-opening .appstore-card-header .apps-table-list-button,
.appstore-card.card-closing .appstore-card-header .apps-table-list-button,
.appstore-card.card-opening .appstore-card-header > img,
.appstore-card.card-closing .appstore-card-header > img,
.appstore-card.card-opening .appstore-card-header-text,
.appstore-card.card-closing .appstore-card-header-text,
.appstore-card.card-opening .appstore-card-close-button,
.appstore-card.card-closing .appstore-card-close-button {
  transition-duration: 400ms;
}
.appstore-card.card-opened .appstore-card-header-text-top .appstore-card-header-text,
.appstore-card.card-opening .appstore-card-header-text-top .appstore-card-header-text {
  transform: translateY(var(--f7-safe-area-top));
}
.appstore-card-subtitle {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 600;
  opacity: 0.55;
}
.appstore-card-title {
  font-size: 28px;
  font-weight: bold;
  max-width: calc(var(--card-width) * 0.8);
  line-height: 1.2;
}
.appstore-card-content {
  color: rgba(0, 0, 0, 0.55);
  font-weight: 500;
}
.appstore-card-content b,
.appstore-card-content strong,
.appstore-card-content em,
.appstore-card-content h1,
.appstore-card-content h2,
.appstore-card-content h3,
.appstore-card-content h4 {
  color: #000;
}
.theme-dark .appstore-card-content {
  color: rgba(240, 240, 255, 0.55);
}
.theme-dark .appstore-card-content b,
.theme-dark .appstore-card-content strong,
.theme-dark .appstore-card-content em,
.theme-dark .appstore-card-content h1,
.theme-dark .appstore-card-content h2,
.theme-dark .appstore-card-content h3,
.theme-dark .appstore-card-content h4 {
  color: #fff;
}
