.apps-table-list {
  --f7-list-bg-color: transparent;
  --f7-list-item-title-white-space: normal;
  --f7-list-item-text-max-lines: 1;
  --f7-list-item-text-font-size: 13px;
  --f7-list-link-pressed-bg-color: transparent;
  --f7-list-item-title-line-height: 1.2;
}
.apps-table-list .item-link .item-inner {
  padding-right: 0;
}
.apps-table-list .item-content {
  padding-left: 0px;
}
.apps-table-list ul {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  height: 234px;
  flex-wrap: wrap;
  overflow: auto;
  padding-left: calc(var(--f7-safe-area-left) + var(--f7-list-item-padding-horizontal));
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
}
.apps-table-list ul::-webkit-scrollbar {
  display: none;
  opacity: 0;
}
@media (min-width: 768px) {
.apps-table-list ul {
    scroll-padding-left: calc(20px + var(--f7-safe-area-left));
}
}
.apps-table-list li {
  width: calc(100% - 20px);
  max-width: 350px;
  margin-right: 10px;
  scroll-snap-align: center center;
}
@media (min-width: 768px) {
.apps-table-list li {
    margin-right: 24px;
    scroll-snap-align: start start;
}
}
.apps-table-list li:nth-child(3n) .item-inner:after {
  display: none;
}
.apps-table-list.list ul:after {
  content: '' !important;
  width: calc(var(--f7-safe-area-right) + var(--f7-list-item-padding-horizontal));
  height: 1px;
  display: block !important;
  position: static;
  transform: none;
  height: 234px;
  background: transparent;
}
.apps-table-list-title {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
}
.apps-table-list-image {
  width: 62px;
  height: 62px;
  border-radius: 21%;
  background: #ccc;
}
.apps-table-list-button {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: var(--f7-list-item-padding-horizontal);
  --f7-button-bg-color: rgba(0, 0, 0, 0.07);
  --f7-button-pressed-bg-color: rgba(0, 0, 0, 0.4);
}
.theme-dark .apps-table-list-button {
  --f7-button-bg-color: rgba(255, 255, 255, 0.11);
  --f7-button-pressed-bg-color: rgba(255, 255, 255, 0.075);
}
.apps-table-list-button span {
  margin-top: 4px;
  font-size: 8px;
  display: block;
  color: var(--f7-list-item-text-text-color);
}
