.featured-apps {
  overflow: auto;
  display: flex;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  margin-top: 0;
}
.featured-apps::-webkit-scrollbar {
  display: none;
  opacity: 0;
}
.featured-apps::after {
  content: '';
  width: calc(var(--f7-block-padding-horizontal) + var(--f7-safe-area-right));
  height: 1px;
  flex-shrink: 0;
}
@media (min-width: 768px) {
.featured-apps {
    scroll-padding-left: calc(20px + var(--f7-safe-area-left));
}
}
.featured-app {
  flex-shrink: 0;
  width: 100%;
  max-width: 350px;
  scroll-snap-align: center center;
  padding: 5px 0px 0px;
  position: relative;
  margin-right: 10px;
}
@media (min-width: 768px) {
.featured-app {
    margin-right: 24px;
    scroll-snap-align: start start;
}
}
.featured-app:before {
  content: '';
  position: absolute;
  background-color: var(--f7-block-strong-border-color);
  display: block;
  z-index: 15;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  height: 1px;
  width: 100%;
  transform-origin: 50% 0%;
  transform: scaleY(calc(1 / var(--f7-device-pixel-ratio)));
}
.featured-app > a {
  color: inherit;
}
.featured-app-headline {
  text-transform: uppercase;
  color: var(--f7-theme-color);
  font-size: 11px;
  font-weight: 500;
}
.featured-app-title {
  font-size: 21px;
  line-height: 1.25;
}
.featured-app-subtitle {
  opacity: 0.54;
  font-size: 21px;
  line-height: 1.25;
}
.featured-app-subtitle:after {
  content: ' ';
  display: inline-block;
}
.featured-app-image {
  width: 100%;
  margin-top: 5px;
  height: 220px;
  border-radius: 5px;
  background: #ccc;
  object-fit: cover;
}
